.Home_Container {
    .header_home {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.4rem 1.4rem;
    }

    .balance {
        display: flex;
        align-items: center;
        flex-direction: column;
        background-color: #0d001a;
        margin: 0 1rem;
        padding: 0.7rem 0;
        border-radius: 0.7rem;

        .account_balance {
            font-size: x-large;
        }

        .action_balance {
            display: flex;
            margin-top: 1rem;


            &>div {
                padding: 0.4rem 1rem;
                display: flex;
                flex-direction: column;
                align-items: center;
                color: #00994d;
                cursor: pointer;
                user-select: none;

                &:active {
                    color: aqua;
                }
            }
        }

        p {
            margin: 0;
        }
    }
}